
* {
  border-radius: 0 !important;
}
h4 {
  color: white;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}
.op {
  opacity: 0.8;
}
.lite {
  background-color: #8CFF92
}
.card-res {
  background-color: rgba(122, 61, 173, 0.7);
  margin: 8px
}
.card-trackName {
  font-family: 'Roboto', sans-serif;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 16px;
  padding-bottom: 16px;
  color: white;
  font-size: 16px;
  font-weight: 500;
}
.card-artistName {
  font-family: 'Roboto', sans-serif;
  padding-top: 35px;
  padding-right: 8px;
  padding-left: 8px;
  padding-bottom: 8px;
  width: 100%;
  font-size: 20px;
  color: white;
  font-weight: 400;
  text-align: left;
  /* -moz-text-align-last: center;
  text-align-last: center; */
}
.card-block {
  background-color: rgba(122, 61, 173, 0.5);
}
.card-art {
  text-align: left;
  padding-left: 8px;
  padding-bottom: 8px;
}
img {
  padding-top: 10px;
}
.btn {
  background-color: rgba(122, 61, 173, 0.7);
  color:  #8CFF92;
  font-size: 20px;
}



